.footer {
	padding-left: 96px;
	padding-top: 29px;
	position: relative;
	p{
		font-size: xx-large;
		position: relative;
		padding-bottom: 142px;
	}
}

@media (main-width : 767px) {
	.footer {
		padding-left: 35dvw !important; 
	}
	
}

.footer ul {
	/*Contains Solcials*/
	display: flex;
	position: absolute;
    left: -13%;
    top: 53px;
}

.footer ul li {
	list-style: none;
}

.footer ul li a {
	width: 50px;
	height: 50px;
	background-color: #fff;
	text-align: center;
	line-height: 50px;
	font-size: 35px;
	margin: 0 10px;
	display: block;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
	border: 3px solid #fff;
	z-index: 1;
}

.footer ul li a .icon {
	line-height: 40px;
	position: relative;
	color: #262626;
	transition: .5s;
	z-index: 3;
}

.footer ul li a:hover .icon {
	color: #fff;
	transform: rotateY(360deg);
}

.footer ul li a:before {
	content: "";
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: 100%;
	background: #f00;
	transition: .5s;
	z-index: 2;
}

.footer ul li a:hover:before {
	top: 0;
}

.footer ul li:nth-child(1) a:before {
	background: #3b5999;
}

.footer ul li:nth-child(2) a:before {
	background: #55acee;
}

.footer ul li:nth-child(3) a:before {
	background: #0077b5;
}

.footer ul li:nth-child(4) a:before {
	background: #dd4b39;
}

@media (max-width: 700px) {

	.icon-title-container,
	.icon {
		display: block;
	}

	.footer ul li a {
		text-decoration: none;
	}

	.footer ul li a i {
		top: 5px;
		left: 1px;
	}
}